body {
    background-color: #f4f4f9;
}

.add-account {
    max-width: 500px;
    margin: 50px auto;
    padding: 20px;
    text-align: center;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.add-account h2 {
    font-size: 2rem;
    color: #333;
    margin-bottom: 1em;
    text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.1);
}

.message {
    margin-bottom: 15px;
    color: #28a745;
    font-weight: bold;
}

.account-form {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.form-group {
    position: relative;
    text-align: left;
}

label {
    font-size: 1rem;
    color: #555;
    display: block;
    margin-bottom: 5px;
}

input[type="text"],
input[type="number"] {
    width: 100%;
    padding: 10px;
    font-size: 1rem;
    border: 2px solid #ddd;
    border-radius: 5px;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
}

input[type="text"]:focus,
input[type="number"]:focus {
    border-color: #84fab0;
    box-shadow: 0 0 8px rgba(132, 250, 176, 0.3);
    outline: none;
}

.submit-button {
    padding: 12px;
    background-color: #84fab0;
    color: #fff;
    border: none;
    border-radius: 5px;
    font-size: 1.1rem;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.3s ease;
}

.submit-button:hover {
    background-color: #56c77c;
    transform: translateY(-2px);
}

.submit-button:active {
    background-color: #3a9f58;
    transform: translateY(0);
}
